import React, { useEffect, useState } from 'react';
import Grid from '@mui/material/Grid';
import Carrusel from './pages/Carrousel';
import Layout from './components/Layout';
import { CluthFrenos } from './core/interface/CluthFrenos';
import axiosClient from './utils/axios';
import Tarjeta from './pages/Tarjeta';
import { Card, CardContent } from '@mui/material';

function App() {
  const [data, setData] = useState<CluthFrenos[]>([]);
  const properties = {
    arrayData: data
  }

  const getAll = () => {
    axiosClient.get('/getall').subscribe({
      next: (res) => {
        setData(res.data);
      },
      error: (err) => {
      },
      complete: () => {

      }
    })
  }

  useEffect(() => {
    getAll();
  }, []);

  return (
    <Layout>
      <Grid container sx={{ justifyContent: 'center' }}>
        <Grid item xs={20}>
          <Card sx={{ width: '100%', height: '100%', paddingTop: '1vh' }} elevation={5}>
            <CardContent>
              {properties.arrayData.length > 0 ? <Carrusel
                {...properties} /> : <Grid />}
            </CardContent>
          </Card>
        </Grid>
        <Card sx={{ width: '100%', height: '100%', paddingTop: '1vh' }} elevation={5}>
          <CardContent>
            {properties.arrayData.map((item, i) => (
              <Grid container sx={{ justifyContent: 'center' }} columns={{ xs: 2, sm: 4, md: 4 }}>
                <Grid item xs={1} sm={2} md={2} key={i}>
                  <Tarjeta arrayData={[item]} key={i} />
                </Grid>
              </Grid>
            ))}
          </CardContent>
        </Card>

      </Grid>
    </Layout>
  );
}

export default App;
